<template>
    <div>
        <!-- 面包屑导航区域 -->
       <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
        <!-- 卡片视图区域、 -->
        <el-card>
            <!-- 搜索  -->
            <el-row :gutter="20">
                <el-col :span="10">
                    <el-input
                     placeholder="请输入内容" 
                     v-model="queryInfo.query"
                      clearable
                      @clear="getUserList"
                     >
                        <el-button
                         slot="append"
                         icon="el-icon-search"
                         @click="getUserList"
                         ></el-button>
                    </el-input> 
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible=true">添加用户</el-button>
                </el-col>
            </el-row>


            <!-- 用户列表 -->
             <el-table :data="userlist" border stripe>
                 <el-table-column type="index" label="#"></el-table-column>
                 <el-table-column label="姓名" prop="username"></el-table-column>
                 <el-table-column label="邮箱" prop="email"></el-table-column>
                 <el-table-column label="电话" prop="mobile"></el-table-column>
                 <el-table-column label="角色" prop="role_name"></el-table-column>
                 <el-table-column label="状态">
                     <template slot-scope="scope">
                         <el-switch
                         @change="userStateChanged(scope.row) "
                            v-model="scope.row.mg_state"
                            >
                        </el-switch>
                     </template>
                 </el-table-column>
                 <el-table-column label="操作">
                     <template slot-scope="scope">
                            <!-- 修改按钮 -->
                       <el-button
                        type="primary"
                        icon="el-icon-edit" 
                        size="mini"
                        @click="showEditDialog(scope.row.id)"
                        ></el-button>
                            <!-- 删除按钮 -->
                       <el-button 
                       type="danger" 
                       icon="el-icon-delete" 
                       size="mini"
                       @click="removeUserById(scope.row.id)"
                       ></el-button>
                            <!-- 分配角色按钮 -->
                       <el-tooltip class="item" effect="dark" content="分配角色 " placement="top" :enterable="false">
                            <el-button type="warning" icon="el-icon-setting" size="mini"></el-button>
                       </el-tooltip>
                     </template>
                 </el-table-column>


             </el-table>
             <!-- 分页区域 -->
             <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[1,2,5,10]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
             </el-pagination>
        </el-card> 
        <!-- 添加用户对话框 -->
        <el-dialog
                title="添加用户"
                :visible.sync="addDialogVisible"
                width="50%"
                @close="addDialogClosed"
            >
            <!-- 内容主体区域 -->
                <el-form
                :model="addForm"
                :rules="addFormRules" 
                ref="addFormRef" 
                label-width="70px" 
                >
                    <el-form-item label="用户名" prop="username">
                        <el-input v-model="addForm.username"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input v-model="addForm.password"></el-input>
                    </el-form-item>
                    <el-form-item label="邮箱" prop="email">
                        <el-input v-model="addForm.email"></el-input>
                    </el-form-item>
                    <el-form-item label="手机" prop="mobile">
                        <el-input v-model="addForm.mobile"></el-input>
                    </el-form-item>
                </el-form>
                <!-- 底部区域 -->
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addDialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="addUser">确 定</el-button>
                </span>
        </el-dialog>
        <!-- 修改用户的对话框 -->
        <el-dialog
            title="修改用户"
            :visible.sync="editDialogVisible"
            width="50%"
            @close="editDialogClosed"
           >
            <el-form 
            :model="editForm" 
            :rules="editFormRules" 
            ref="editFormRef" 
            label-width="70px" 
            >
                <el-form-item label="用户名">
                    <el-input :disabled="true" v-model="editForm.username"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="editForm.email"></el-input>
                </el-form-item>
                <el-form-item label="手机" prop="mobile">
                    <el-input v-model="editForm.mobile"></el-input>
                </el-form-item>
                
            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="editUserInfo">确 定</el-button>
            </span>
        </el-dialog>

    </div>
</template>

<script>
export default {
  data() {
    //   验证邮箱的规则
      var checkEmail=(rule,value,cd)=>{
        //   验证邮箱的正则表达式  
        const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
        if(regEmail.test(value)){
            // 合法的邮箱
            return cd()
        }
        cd(new Error("请输入合法的邮箱"))
      }
    //   验证手机号的规则
      var checkMobile=(rule,value,cd)=>{
        // 验证手机号的正则表达式
        const regMobile=/^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
        if(regMobile.test(value)){
            return cd()
        }
        cd(new Error('请输入合法的手机号'))
      }
    return {
      // 获取用户列表的参数对象
      queryInfo: {
        query: "",
        // 当前的页数
        pagenum: 1,
        // 当前每页显示多少条数据
        pagesize: 2
      },
      userlist: [],
      total: 0,
      // 控制显示对话框的显示和隐藏
      addDialogVisible: false,
    //   控制修改用户对话框的显示和隐藏
       editDialogVisible:false,
    //    查询到的用户信息对象
        editForm:{},
        
      // 添加用户的表单数据
      addForm: {
          username:'',
          password:'',
          email:'',
          mobile:''
      },
      // 添加表单的验证规则对象
      addFormRules: {
        username: [
            {
             required: true,
             message: "请输入用户名",
             trigger: "blur" 
             },
             {
                 min:3,
                 max:10,
                 message:'用户名的长度在3~10个字符之间',
                 trigger:'blur'
             }
            ],
        password:[
             {
             required: true,
             message: "请输入用密码",
             trigger: "blur" 
             },
             {
                 min:6,
                 max:15,
                 message:'密码 的长度在6~15个字符之间',
                 trigger:'blur'
             }
        ],
        email:[
              {
             required: true,
             message: "请输入用邮箱",
             trigger: "blur" 
             },
             {
                 validator:checkEmail,
                 trigger:'blur'
             }
        ],
        mobile:[
             {
             required: true,
             message: "请输入用手机号",
             trigger: "blur" 
             },
             {
                 validator:checkMobile,
                 trigger:'blur'
             }
             
        ]
      },
      // 修改表单的验证规则对象
        editFormRules:{
            email:[
                {
             required: true,
             message: "请输入用邮箱",
             trigger: "blur" 
             },
             {
                 validator:checkEmail,
                 trigger:'blur'
             }
            ],
            mobile:[
                 {
             required: true,
             message: "请输入用手机号",
             trigger: "blur" 
             },
             {
                 validator:checkMobile,
                 trigger:'blur'
             }
            ]
        },

     
      //str:0
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get("users", {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) {
        return this.$message.error("获取用户列表失败！");
      }
      this.userlist = res.data.users;
      console.log(this.userlist);
      this.total = res.data.total;
      console.log(res);
    },
    //    监听pagessize 改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getUserList();
    },
    //    监听 页码值 改变的事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getUserList();
    },
    //    监听switch 开关状态的改变
    async userStateChanged(userinfo) {
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      );
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state;
        return this.$message.error("更新用户状态失败！");
      }
      this.$message.success("更新用户状态成功！");
    },
    // 监听添加用户对话框的关闭事件
    addDialogClosed(){
        this.$refs.addFormRef.resetFields()
    },
    // 点击按钮，添加新用户
    addUser(){
        this.$refs.addFormRef.validate( async valid=>{
            if(!valid) return
            //可以发起添加用户的网络请求
           const {data:res} = await this.$http.post('users',this.addForm)
           if(res.meta.status !==201){
               this.$message.error('添加用户失败')
           }
           this.$message.error('添加用户成功')
        //    隐藏添加用户对话框
           this.addDialogVisible=false
        //    重新获取用户列表数据、
           this.getUserList()

        })
    },
    // 展示编辑用户的对话框
     async showEditDialog(id){
        console.log(id)
       const {data:res} = await this.$http.get('users/'+id)
       if(res.meta.status !==200){
           return this.$message.error('查询用户信息失败！')
       }
       this.editForm=res.data
       console.log(this.editForm)
        this.editDialogVisible=true
    },
    // 监听修改用户对话框的关闭事件
    editDialogClosed(){
        this.$refs.editFormRef.resetFields()
    },
    // 修改用户信息并提交
    editUserInfo(){
        this.$refs.editFormRef.validate(async valid =>{
            if(!valid)return 
            //发起修改用户信息的数据请求
            const {data:res} = await this.$http.put('users/'+this.editForm.id,
            {
                email:this.editForm.email,
                mobile:this.editForm.mobile
            }
            )
            if(res.meta.status !==200){
                return this.$message.error('更新用户信息失败')
            }
            // 关闭对话框
            this.editDialogVisible=false
            // 刷新数据列表
            this.getUserList()
            // 提示修改成功
            this.$message.success('更新用户信息成功！')

        })
    },
    // 根据Id删除对应的用户信息
    async removeUserById(id){
        console.log(id)
        // 弹框询问用户是否删除数据
       const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
        // 如果用户确认删除，则返回值为字符串 confirm
        // 如果用户取消了删除,则返回值为字符串cancel
        if(confirmResult !=='confirm'){
            return this.$message.info('已取消删除')
        }
        const {data:res} = await this.$http.delete('users/'+id)
        if(res.meta.status !==200){
            return this.$message.error('删除用户失败')
        }
        this.$message.success('删除用户成功')
        this.getUserList()
        console.log(confirmResult)
    }

    //数据更新
    //add(){
    //this.str++
    //},
    //数据销毁
    //de(){
    // this.destroy()
    //}
  }
  //组件创建之前调用
  //beforeCreate() {
  //console.log('vue...............beforeCreate---组件创建之前调用')
  //},
  //组件创建加载完成
  //created() {
  //console.log('vue...............created---组件创建加载完成')
  //},
  //数据挂在之前(渲染)
  //beforeMount() {
  //console.log('vue...............beforeMount---数据挂在之前(渲染)')
  //},
  //数据挂在完成(渲染)
  //mounted() {
  //.log('vue...............mounted---数据挂在完成(渲染)')
  //},
  //数据更新之前
  //beforeUpdate() {
  //console.log('vue...............beforeUpdate---数据更新之前')
  //},
  //数据更新完成
  //updated() {
  //onsole.log('vue...............updated---数据更新完成')
  //},
  //数据销毁之前
  //beforeDestroy() {
  //console.log('vue...............beforeDestroy---数据销毁之前')
  //},
  //数据销毁完成
  //destroyed() {
  //console.log('vue...............destroyed---数据销毁完成')
  //},
  //watch:{
  //route(){
  //console.log('vue...............destroyed---监听属性')
  //}
  //}
};
</script>

<style lang="less" scoped>
</style>
